@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display&family=Poppins:wght@200;300;400;500;600;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=EB+Garamond:wght@400;500;600;700&display=swap');

.Home {
  background: #6F79FF;
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
}
.homeDesign{
  width: 100vw;
  height: 90vw;
  display: flex;
}
.logo{
  padding: 20px 0 0 20px;
  height: 10vh;
}
.logoAdjust{
  height: 5vh;
}
.text {
  position: relative;
  width: 50vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  left: 15%;
}
.image {
  width: 50vw;
}
.button{
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
  padding:30px;
  border-radius: 5px;
  width: 300px;
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
  font-size: 1.5rem;
  color: #3D4CCD;
  filter: drop-shadow(10px 10px 10px rgba(0,0,0,0.25));
}
.button:hover {
  transform: scale(1.1);
  transition: all ease-in-out 0.3s; 
}
.imageAdjust {
  width: 50vw;
}

.homeText2 {
  font-family: 'EB Garamond', serif;
  font-weight: 500;
  font-size: 4rem;
  color: white;
}
.homeText1 {
  /*font-family: 'EB Garamond', serif;*/
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
  font-size: 4rem;
  color: white;
}
.social {
  position: relative;
  bottom: 10px;
  padding: 20px;
  fill: white;
}
.socialicon {
  padding-left: 20px;
  height: 20px;
  fill: white;
}
.socialicon:hover {
  transform: scale(1.5);
  transition: all ease 0.3s; 
}

@media only screen and (max-width: 600px) {
  .homeDesign {
    flex-direction: column;
    height: 100vh;
    justify-content: space-between;
  }
  .text {
    width: 100%;
  }
  .homeText1 {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 2.5rem;
    color: white;
  }
  .button {
    border-radius: 50px;
    padding: 20px;
    font-size: 1.2rem;
    width: 60vw;
  }
  .image{
    width: 100%
  }
  .imageAdjust{
    width: 100%
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}